  </b-row>
 <h4><span class="heading5">About</span></h4>
 <p>Fashion for Change is a non-profit organization based at the University of Waterloo and Wilfred Laurier University. It’s the largest student-run charity in the Kitchener - Waterloo region. They raise up to $20,000 the WE Villages Program. </p>
  <h4><span class="heading5">Goals</span></h4>
  <p>In November of 2017, I was hired onto their design team to work on a re-design of their website. Their old website was designed over 5 years ago. It didn’t adapt to different screen sizes, needed a branding and general refresh.</p>
    <h2 class="header-padding"><a class="font-deco"> Our team of 3 was tasked with re-designing the user experience, the general design, the job application process, and improve sponsor information.</a></h2>
  <h4><span class="heading5">My Role</span></h4>
<p>I was hired on as the User Experience Designer. As part of my role, I worked on improving the site architecture, the user experience, and I introduced the initial design feel that we ended up implementing. </p>
  <h4><span class="heading5">Old Site Review</span></h4>
  <p>One of the first things we did as part of our re-design was to analyze the old website and figure out what problems people were running into. To do this we did user testing with potential customers and user groups. Some of the things we noticed were that many of the users complained first and foremost about the design. It is simply unappealing and very outdated. A second thing that people had difficulty with was finding information, there is a disconnect between the information that we were trying to communicate and what the website was showing. Other insights included difficulty applying to jobs and finding information for sponsors. </p>
<h2 class="header-padding"><a class="font-deco">"I had no idea how to find anything."</a></h2>
